}
.card__wrapper--atlantis {
  background-image: image-set("../img/atlantis.webp" 1x, "../img/atlantis@2x.webp" 2x, "../img/atlantis.jpg" 1x, "../img/atlantis@2x.jpg" 2x);
  background-position: -114px 0;
}
.card__wrapper--odissei {
  background-image: image-set("../img/odissei.webp" 1x, "../img/odissei@2x.webp" 2x, "../img/odissei.jpg" 1x, "../img/odissei@2x.jpg" 2x);
  background-position: -114px 0;
}
.card__wrapper--olimpia {
