  background-position: -114px 0;
}
.card__wrapper--aurora {
  background-image: image-set("../img/aurora.webp" 1x, "../img/aurora@2x.webp" 2x, "../img/aurora.jpg" 1x, "../img/aurora@2x.jpg" 2x);
  background-position: -114px 0;
}
.card__wrapper--leonor {
  background-image: image-set("../img/leonor.webp" 1x, "../img/leonor@2x.webp" 2x, "../img/leonor.jpg" 1x, "../img/leonor@2x.jpg" 2x);
  background-position: -114px 0;
