.form {
  background-color: #011c40;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
}
.form h3 {
  margin: 0 0 35px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
.form input {
  width: 100%;
  margin: 0 0 20px;
  padding: 11px 16px;
  font-family: inherit;
  font-size: 14px;
  line-height: 21px;
  background-color: #f9fbfd;
  border: none;
}
.form input::placeholder {
  font-family: inherit;
  font-size: 14px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}
.form input:hover {
  opacity: 0.5;
}
.form input:active {
  opacity: 0.3;
}
.form label:last-of-type {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 0 30px;
  padding: 0 0 0 23px;
  font-size: 10px;
  line-height: 14px;
  color: #ffffff;
  cursor: pointer;
}
@media (max-width: 767px) {
  .form label:last-of-type {
    padding: 0 0 0 28px;
    line-height: 12px;
  }
}
.form label:last-of-type span::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  background: #f9fbfd;
}
@media (max-width: 767px) {
  .form label:last-of-type span::before {
    width: 20px;
    height: 20px;
  }
}
.form label:last-of-type input[type=checkbox]:checked + span::before {
  background: url("../img/sprite/tick.svg") no-repeat 50% 50%, #f9fbfd;
}
.form label:last-of-type input[type=checkbox]:focus-within + span::before {
  outline: 1px solid #011c40;
}
.form label:last-of-type input[type=checkbox]:hover + span::before {
  opacity: 0.5;
}
.form button {
  width: 100%;
  padding: 11px;
  border: none;
  font-family: inherit;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  cursor: pointer;
}
.form button:hover {
  opacity: 0.5;
}
.form button:active {
  opacity: 0.3;
}

.main-footer {
  background-color: #011c40;
}

.main-footer__logo {
  display: flex;
}

.main-footer__navigation {
  justify-self: start;
}
.main-footer__navigation .navigation__link {
  font-size: 12px;
  line-height: 15px;
}

.social__list {
